$ns: "fdt-";
@import 'style/global.scss';

.#{$ns}AddressSimple {
    &__container {
        display: flex;
        justify-content: start;
        align-items: center;
        flex-direction: column;
        height: 100%;
        padding: 100px 200px;
    }

    &__input-container {
        display: flex;
    }

    &__input {
        width: 1100px;
        height: 120px;

        font-size: 40px;
        padding: 0 20px;


        border: 1px solid rgba(255, 255, 255, 0.7);
        border-radius: 10px;
    }

    &__button {
        margin-left: 30px;
    }

    &__suggest-list {
        margin-top: 50px;
        width: 1100px;

        overflow-y: scroll;
    }

    &__suggest-item {
        padding: 20px 40px;
        border-radius: 16px;
        font-size: 46px;
        color: #fff;
        margin-bottom: 20px;

        background-color: rgba(255, 255, 255, .1);

        &:focus {
            background-color: rgba(255, 255, 255, .3);
        }
    }
}
