$ns: "fdt-";
@import 'style/global.scss';

.#{$ns}AddressMap {
    position: relative;

    display: flex;
    flex-direction: column;

    height: 100%;
    width: 100%;

    &__address {
        display: flex;
        align-items: center;
        justify-content: center;
        font-size: 40px;

        height: 200px;
        color: #fff;
    }

    &__map {
        height: 880px;
        width: 100%;
    }

    &__pointer {
        position: absolute;
        top: calc(50% - 20px);
        left: calc(50% - 66px);

        display: flex;
        flex-direction: column;

        align-items: center;

        width: 132px;
        height: 124px;
    }

    &__pointer-body {
        position: relative;

        width: 132px;
        height: 86px;

        background: #E12D39;
        border-radius: 108px;

        display: flex;

        flex-direction: column;

        align-items: center;
        justify-content: center;
    }

    &__pointer-name {
        color: #fff;
        height: 34px;
        font-size: 32px;
        font-weight: 600;
        line-height: 34px;
        text-align: center;
    }

    &__pointer-desc {
        height: 20px;
        color: rgba(255, 255, 255, 0.8);
        font-size: 24px;
        font-weight: 500;
        line-height: 20px;
        text-align: center;
    }

    &__pointer-kernel {
        //position: absolute;
        //top: 86px;
        height: 34px;
        width: 4px;

        background: #E12D39;
        border-bottom-left-radius: 2px;
        border-bottom-right-radius: 2px;
    }
}
